@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-sans-regular.woff2") format("woff2"), url("../fonts/pt-sans-regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "WorkSans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/work-sans.woff2") format("woff2"), url("../fonts/work-sans.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

main {
  flex: 1 0 auto;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 665px;
  margin: 0 auto;
  box-sizing: border-box;
}

.page-main {
  background-color: #f4f5fc;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .page-main {
    align-items: flex-start;
  }
}
.page-main__wrapper {
  display: grid;
  grid-template-columns: 138px 1fr;
  position: relative;
}
@media (max-width: 767px) {
  .page-main__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.title-section {
  width: 138px;
  height: 482.5px;
  background-color: #5a61ed;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 22px 6px 22px 22px;
}
@media (max-width: 767px) {
  .title-section {
    width: 100%;
    height: auto;
    padding: 15px 22px 15px 22px;
    align-items: center;
  }
}

.title-section__title {
  font-family: "PT Sans", "Arial", sans-serif;
  color: #ffffff;
  font-weight: 400;
  font-size: 49px;
  line-height: 63px;
  letter-spacing: 0.5px;
  writing-mode: vertical-rl;
  transform: scale(-1);
  height: 380px;
  user-select: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}
@media (max-width: 767px) {
  .title-section__title {
    height: auto;
    width: 100%;
    writing-mode: initial;
    transform: none;
    line-height: 63px;
  }
}

.sign-up {
  padding: 39px 20px 33px 22px;
  background-color: #ffffff;
  width: 525px;
  overflow: hidden;
}
@media (max-width: 767px) {
  .sign-up {
    width: 100%;
  }
}

.sign-up-form {
  position: relative;
}
.sign-up-form form {
  position: relative;
  z-index: 1;
}
.sign-up-form h2 {
  font-family: "WorkSans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -0.46px;
  margin: 2px 0 0 0;
}
@media (max-width: 767px) {
  .sign-up-form h2 {
    margin: 0 0 10px 0;
  }
}
.sign-up-form__description {
  font-family: "WorkSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: -0.4px;
  margin: 0 0 20px 0;
}
.sign-up-form__description p {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  margin: 0 0 10px 0;
}
.sign-up-form label {
  font-family: "WorkSans", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.4px;
  color: #7c7c7c;
}
@media (max-width: 767px) {
  .sign-up-form label {
    width: 100%;
  }
}
.sign-up-form span {
  font-family: "WorkSans", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: -0.4px;
  color: #7c7c7c;
}
.sign-up-form input {
  background-color: transparent;
}
@media (max-width: 767px) {
  .sign-up-form input {
    margin-bottom: 15px;
  }
}
.sign-up-form input::placeholder {
  font-family: "WorkSans", "Arial", sans-serif;
  letter-spacing: -0.4px;
  color: #000000;
  font-weight: 400;
}
.sign-up-form input:not([type=radio]) {
  width: 235px;
  border: none;
  border-bottom: 1px solid #f2f2f2;
  outline: none;
  padding: 5px 0 7px 0;
  cursor: pointer;
}
@media (max-width: 767px) {
  .sign-up-form input:not([type=radio]) {
    width: 100%;
  }
}
.sign-up-form button {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  padding: 7px 26px;
  color: #ffffff;
  background: #5a61ed;
  border: 0.5px solid #ffffff;
  cursor: pointer;
}
.sign-up-form select {
  position: relative;
  width: 235px;
  border: none;
  border-bottom: 1px solid #f2f2f2;
  padding: 5px 10px 7px 0;
  margin: 0 0 0 0;
  cursor: pointer;
  text-align: start;
  background-color: transparent;
  appearance: none;
  z-index: 0;
}
@media (max-width: 767px) {
  .sign-up-form select {
    width: 100%;
  }
}
.sign-up-form__select-wrapper:not(:last-child) {
  margin-right: 10px;
}
.sign-up-form__select-wrapper {
  position: relative;
}
@media (max-width: 767px) {
  .sign-up-form__select-wrapper {
    width: 100%;
  }
}
.sign-up-form__select-wrapper::before {
  content: "";
  width: 8px;
  height: 4px;
  position: absolute;
  top: 40%;
  right: 0;
  background-image: url("../../img/svg/select-icon.svg");
  background-repeat: no-repeat;
  z-index: 1;
}
@media (max-width: 767px) {
  .sign-up-form__select-wrapper {
    margin-bottom: 15px;
  }
}
.sign-up-form__background {
  position: absolute;
  bottom: -34px;
  right: -45px;
  z-index: 0;
}
.sign-up-form__field {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sign-up-form__field--column {
  flex-direction: column;
  align-items: flex-start;
}
@media (max-width: 767px) {
  .sign-up-form__field--column {
    width: 100%;
  }
}
.sign-up-form__field--row {
  display: flex;
  flex-direction: row;
  margin: 0 0 16px 0;
}
@media (max-width: 767px) {
  .sign-up-form__field--row {
    flex-direction: column;
  }
}
.sign-up-form__field--submit {
  margin: 94px 0 0 0;
}
@media (max-width: 767px) {
  .sign-up-form__field--submit {
    margin: 54px 0 0 0;
  }
}
.sign-up-form__field--submit p {
  font-family: "WorkSans", "Arial", sans-serif;
  font-size: 13px;
  line-height: 15px;
  margin: 0 0 0 -2px;
}
.sign-up-form__wrapper {
  display: flex;
}
@media (max-width: 767px) {
  .sign-up-form__wrapper {
    width: 100%;
  }
}
.sign-up-form__input-wrapper {
  position: relative;
}
.sign-up-form__link {
  font-family: "WorkSans", "Arial", sans-serif;
  text-decoration: underline;
  color: #5a61ed;
  margin: 0;
}
.sign-up-form__wrapper--gender {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .sign-up-form__wrapper--gender {
    margin-bottom: 0;
    margin-top: 7px;
  }
}
.sign-up-form__wrapper--gender label {
  color: #000000;
  flex-direction: row;
  align-items: center;
  cursor: pointer;
  padding: 0;
  position: relative;
}
.sign-up-form__wrapper--gender span {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 15px;
  height: 15px;
  cursor: pointer;
  margin: 0 6px 0 0;
}
.sign-up-form__wrapper--gender input[type=radio]:checked + label span::after {
  content: "";
  width: 5px;
  height: 5px;
  background-color: #5a61ed;
  border-radius: 50%;
  position: absolute;
  top: 27%;
  left: 26%;
}
@media (max-width: 767px) {
  .sign-up-form__wrapper--gender input[type=radio]:checked + label span::after {
    top: 27.5%;
    left: 29%;
  }
}
.sign-up-form__wrapper--gender label:not(:last-child) {
  margin-right: 24px;
}
.sign-up-form__wrapper--column {
  flex-direction: column;
}
.sign-up-form .sign-up-form__day {
  width: 56px;
}
@media (max-width: 767px) {
  .sign-up-form .sign-up-form__day {
    width: 100%;
  }
}
.sign-up-form .sign-up-form__month {
  width: 91px;
}
@media (max-width: 767px) {
  .sign-up-form .sign-up-form__month {
    width: 100%;
  }
}
.sign-up-form .sign-up-form__year {
  width: 68px;
}
@media (max-width: 767px) {
  .sign-up-form .sign-up-form__year {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .sign-up-form .sign-up-form__wrapper--birth {
    width: 100%;
  }
}
.sign-up-form .sign-up-form__wrapper--birth select:not(:last-child) {
  margin: 0 0 0 -4px;
}
.sign-up-form .sign-up-form__field--birth .sign-up-form__field:not(:last-child) {
  margin: 0 10px 0 0;
}
.sign-up-form .is-accepted {
  position: relative;
}
.sign-up-form .is-accepted::before {
  content: "";
  width: 11px;
  height: 8px;
  background-image: url("../../img/svg/accepted-icon.svg");
  background-repeat: no-repeat;
  z-index: 1;
  position: absolute;
  bottom: 27%;
  right: 0%;
}
@media (max-width: 767px) {
  .sign-up-form .is-accepted::before {
    bottom: 45%;
  }
}
.sign-up-form input.is-wrong {
  border-color: #f12424;
  color: #f12424;
}
.sign-up-form input.is-accept {
  border-color: #47c11b;
  color: #000000;
}

.is-success {
  display: none;
}

.form-success {
  display: none;
  grid-column: 2/3;
  grid-row: 1/2;
  padding: 38px 22px 32px 21px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .form-success {
    width: 100%;
    height: 80vh;
  }
}
.form-success h2 {
  font-family: "WorkSans", "Arial", sans-serif;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -0.46px;
  margin: 0 0 0 43px;
}
@media (max-width: 767px) {
  .form-success h2 {
    margin: 0 0 5px 0;
  }
}
.form-success p {
  font-family: "WorkSans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  color: #111111;
  margin: 11px 0 0 43px;
}
@media (max-width: 767px) {
  .form-success p {
    margin: 0;
  }
}
.form-success .form-success__text-wrapper {
  align-self: flex-end;
  margin-bottom: 58px;
}
@media (max-width: 767px) {
  .form-success .form-success__text-wrapper {
    margin: 100px 0 100px 0;
  }
}
.form-success .form-success__link {
  margin: 0;
  align-self: flex-end;
}

.shaking-button {
  animation: shaking-button 0.4s linear 1.5s;
  animation-delay: -0.1s;
}

@keyframes shaking-button {
  0% {
    transform: translateX(5px);
  }
  15% {
    transform: translateX(-10px);
  }
  30% {
    transform: translateX(10px);
  }
  45% {
    transform: translateX(-10px);
  }
  60% {
    transform: translateX(10px);
  }
  75% {
    transform: translateX(-10px);
  }
  90% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(-5px);
  }
}
